.formfield {
    width: 100%;
    height: 48px;
    border-radius: 8px;
    border: 0;
    padding-inline: 16px;
    box-sizing: border-box;
    
    
    color: #7E7E83;
    background-color: #f8f8fa;

    &:hover {
      border: 1px solid #d5d7de;
    }

    &:focus {
      outline: none;
      border: 1px solid #292929;;
    }

    &::placeholder {
      font-family: Space Mono;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%; 
    }

    &--textarea {
      height: 120px;
      padding: 14px 16px;
      resize: none;
    }
  }
  